import React from 'react';
import './totalrevenue.scss';

const TotalRevenue = ({ array }) => {

  function getTotal(array) {
      // Le total est intialis  0
      let total = 0;
      array.map((item) => {
        // Chacun des lments du tableau multiplie sa quantit  son prix  l'unit
        let value = item.quantity * item.unit_price;
        // Les valeurs sont ajoutes au total
        total += value;
      })
      return total;
    }

  // Cette fonction permet de sparer les grands nombres par des virgules
  function numberWithCommas(number) {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }

  // La valeur de getTotal est envoye dans numberWithCommas pour lui insrer des virgules
  // toFixed(2) ne laisse que deux chiffres aprs la virgule  la valeur de getTotal
  const totalWithCommas = numberWithCommas(getTotal(array).toFixed(2));

  return (
    <div className='total'>
      <span className='total__title'>Revenue</span>
      <span className='total__counter'>{totalWithCommas} </span>
    </div>
  )
};

export default TotalRevenue;
